<!-- 购物车 -->
<template>
	<div style="background-color: white;">
		<Navigation></Navigation>
		<!-- 搜索框和logo -->
		<SearchAndlogobox></SearchAndlogobox>
	<div style="background-color:#DDDDDD">
		<br>
				<div class="container" style=" height: 450px; background-color: white; border-radius: 30px;">
				<div class="d-flex align-items-center" 
							style="height: 80px; border-bottom: 1px solid #CCCCCC;">
					<h4>购物车</h4>
					<h4>（全部{{	tableData.length}}）</h4>
				</div>
				
				<div>
		<el-table
		height="320"
    :data="tableData"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
		
		<el-table-column
			type="selection"
			width="55">
			</el-table-column>
			
		<el-table-column
		label="商品信息"
		width="400"
		>
		<template slot-scope="scope">
		<div class="d-flex justify-content-between" >
			<img :src="require('@/assets/'+scope.row.commoditypicture)" width="80px" height="65px" style="cursor: pointer;" @click="commoditydetailed(scope.row.commodityid)" >
			<p class="ml-2" style="width: 200px; font-size: 12px;">
			{{scope.row.commodityname}}</p>
			<p class="ml-2" style="width: 200px; font-size: 12px;">
				商品规格：{{scope.row.commodityspecification}}</p>
		</div>
		</template>
		</el-table-column>
		
		<el-table-column
		prop="commodityprice"
		label="单价"
		width="120">
		<template slot-scope="scope">
        <span>￥{{scope.row.commodityprice}}</span>
    </template>
		</el-table-column>
		
    <el-table-column
		type="index"
		prop="commoditycount" 
		label="数量" width="200">
		<template slot-scope="scope">
			<el-input-number size="mini" 
			@change="addcount(scope.row.id,scope.row.commoditycount,scope.row.commodityprice,scope.$index)" 
			v-model="scope.row.commoditycount" :min="1" :max="10">
			</el-input-number>
		</template>
    </el-table-column>
		
		<el-table-column
		
		label="金额">
		<template slot-scope="scope">
		<span style="color: #FF0000; font-weight: bold;">￥{{scope.row.totalprice}}</span>
		</template>
		</el-table-column>
		
	<el-table-column
		label="操作"
		width="180px"
		type="index">
		<template slot-scope="scope">
			<el-button icon="el-icon-delete" type="primary" 
			@click="delshoppingtrolley(scope.row.id,scope.$index)">删除</el-button>
		</template>
		</el-table-column>
  </el-table>
	<div class="mt-1 d-flex align-items-center justify-content-between ">
		<span>已选商品{{multipleSelection.length}}件</span>
		<div class="col-4 d-flex align-items-center justify-content-around">
		<span>金额：<span style="color: #F0AD4E; font-weight: bold; font-size: 20px;">￥{{totalpriceAll}}</span></span>	
		<el-button type="warning" @click="balance" round>结算</el-button>
		</div>
	</div>
				</div>
				
				</div>
				<br>
	</div>
	<h1>123123</h1>
	</div>
</template>

<script>
	import Navigation from'../components/Navigation.vue'
	import SearchAndlogobox from '../components/SearchAndlogobox.vue'
	import {Ajax} from '../assets/js/ajax.js'
	export default{
		name:'ShoppingtrolleyPage',
		components:{
			Navigation,
			SearchAndlogobox
		},
		data:function(){
			return{
				tableData: [ 
					{
					commodityname:"连衣裙",
					commodityspecification:"红色，M",
					commodityprice: 125,
					commoditycount:2,
					totalprice:123,
					commoditypicture:"imgs/ca_2.jpg"
					},
					{
					commodityname:"连衣裙2",
					commodityspecification:"红色，M",
					commodityprice: 125,
					commoditycount:2,
					totalprice:123,
					commoditypicture:"imgs/ca_2.jpg"
					}
				],
				user:"",
				multipleSelection:"",
				shoppingtrolleyId:[],
				totalpriceAll:0
			}
		},
		methods:{
			//进入商品详情
			commoditydetailed:function(productId){
				this.$router.push({
						name:'CommoditydetailedPage',
						params:{id:productId}
				})
			},
			//改变数量
			addcount(id,count,price,index){
				var dataUpdate = "id="+id+"&totalprice="+price*count
				+"&commoditycount="+count+"&uid="+this.user.id;
				Ajax("post","shoppingtrolley/updateShoppingCount",dataUpdate,(dataUpdate)=>{
					this.tableData[index].totalprice=dataUpdate.totalprice;
					//重新计算选中的总金额
					this.totalpriceAll=0;
					for(let i=0;i<this.multipleSelection.length;i++){
						this.totalpriceAll=(this.totalpriceAll+this.multipleSelection[i].totalprice)
					}
					})
			},
			
      handleSelectionChange(val) {
        this.multipleSelection = val;
				//计算选中的总金额
				this.totalpriceAll=0;
				for(let i=0;i<this.multipleSelection.length;i++){
					this.totalpriceAll=(this.totalpriceAll+this.multipleSelection[i].totalprice)
				}
      },
			
			balance (){//结算
			if(this.multipleSelection==""){
				alert("请选择商品在进行结算!")
				return
			}
			
			var dataconsigneeInfo="uid="+parseInt(this.user.id);
			Ajax("post","consigneeInfo/queryConsigneeInfouid",dataconsigneeInfo,(data)=>{
				if(data.consigneeaddress==""||data.consigneename==""||data.consigneephone==""){
					alert("请完善收货信息在进行购买");
				}else{
					this.shoppingtrolleyId=[];
					for(let i=0;i<this.multipleSelection.length;i++){
						/* this.shoppingtrolleyId+=this.multipleSelection[i].id; */
						this.shoppingtrolleyId.push(this.multipleSelection[i].id)
					}
					
					var addorder="id="+this.shoppingtrolleyId;
					Ajax("post","shoppingtrolley/addorder",addorder,(addorder)=>{
						alert(addorder.message)
					})
				}
				
			})
				
			},
			delshoppingtrolley(shoppingId,index){//删除多个购物车
				this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
					this.shoppingtrolleyId=[];
					this.shoppingtrolleyId+=shoppingId;
					
					var datadel = "shoppingtrolleyId="+this.shoppingtrolleyId;
					Ajax("post","shoppingtrolley/delShoppingtrolley",datadel,(datadel)=>{
						if(datadel.success==true){
							this.$message({
							message: datadel.message
							});
							this.tableData.splice(index,1)
						}else{
						alert(datadel.message)
					}
					
					//查询当前用户的购物车
/* 					var dataShoppingTrolley="uid="+parseInt(this.user.id);
					Ajax("post","shoppingtrolley/queryShoppingtrolleyuid",dataShoppingTrolley,(dataShopping)=>{
						this.tableData=dataShopping
						}) */
					})
        }).catch(() => {
          this.$message({
            message: '已取消删除'
          });          
        });
			}
},
		created:function(){
			//查询当前用户
			Ajax("get","user/homePageUser",null,(data)=>{
				this.user=data
					//查询当前用户的购物车
					var dataShoppingTrolley="uid="+parseInt(data.id);
					Ajax("post","shoppingtrolley/queryShoppingtrolleyuid",dataShoppingTrolley,(dataShopping)=>{
						this.tableData=dataShopping
					})
			}) 
		}
	}
</script>

